/* Skills */
.skills {
    margin-top: 5rem;
    text-align: -webkit-center;
  }
  .lineHorizontal {
    height: 2px;
    background-color: #356daf;
    width: 100%;
  }
  .rowLines {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .box {
    background-color: #161616;
    border-radius: 25px;
    padding: 1rem;
    text-align: -webkit-center;
  }
  .skills h1 {
    color: #356daf;
    font-size: 22px;
  }
  .containerSkills {
    display: inline-table;
    margin: auto;
    text-align: -webkit-center;
  }
  .skills img {
    width: 8vh;
    margin: 1rem;
  }
  .reactLogo {
    width: 13vh !important;
  }