/* Description */
.photo {
    background-image: url("../../assets/img/Photo.png");
    background-color: #161616;
    width: 100%;
    display: flex;
    justify-content: center;
    background-repeat: no-repeat;
    background-origin: content-box;
    background-size: cover;
    background-position: center center;
    position: relative;
    clip-path: polygon(0px 0px, 100% 0px, 100% 92%, 0px 100%);
  }
  .description {
    margin: 2rem;
    display: flex;
    flex-direction: column;
  }
  .description h3 {
    color: #356daf;
    font-size: 4vh;
    margin: 1px;
  }
  .description h1 {
    font-size: 7vh;
    margin: 0px;
  }
  .description h2 {
    color: #ffffff;
    font-size: 3vh;
    margin: 0px;
  }
  .description p {
    color: #ffffff;
    font-size: 2.5vh;
    margin-top: 10px;
  }
  .mail {
    margin-top: 25px;
    border: 2px solid #356daf;
    background-color: #356daf;
    padding: 1rem;
    border-radius: 10px;
    width: 200px;
    font-size: 16px;
    color: #ffffff;
  }
  
  
/*pc*/
@media (min-width: 1024px) {
  .header {
    height: 80vh;
    padding-top: 0px;
  }
}
/*Movil*/
@media (min-width: 768px) {
  .header {
    padding-top: 84px;
  }
}
