/* Nav */
a {
  text-decoration: none;
  color: #356daf;
  font-weight: bold;
}

.Nav {
  font-size: 28px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-left: 2rem;
  padding-right: 2rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  color: #356daf;
  background-color: #161616;
  box-shadow: inset -2px -5px 20px 0px #000000;
  position: sticky;
  z-index: 3;
  top: 0;
}

.row {
  display: flex;
  flex-direction: row;
  margin: 0;
}

.column {
  display: flex;
  flex-direction: column;
}
.hamburguesa {
  align-items: flex-end;
}
li {
  float: left;
  list-style: none;
}
li a {
  font-size: 16px;
}
.category {
  display: none;
  flex-direction: column;
  position: absolute;
  right: 7vh;
  top: 8vh;
  background-color: #161616;
  border-radius: 10px;
}
.category-active {
  display: flex;
  flex-direction: column;
  position: absolute;
  right: 7vh;
  top: 8vh;
  background-color: #161616;
  border-radius: 10px;
}
ul {
  display: flex;
  flex-direction: column;
  margin: 1rem;
  padding: 0;
}

/* Lines */
.menuButton {
  background-color: #161616;
  border: 0px;
}
button:active {
  border: 0;
}
.line1 {
  width: 35px;
  background-color: #356daf;
  height: 2px;
  margin-top: 0;
}
.line2 {
  width: 33px;
  background-color: #356daf;
  height: 2px;
  margin-top: 10px;
}
.line3 {
  width: 31px;
  background-color: #356daf;
  height: 2px;
  margin-top: 10px;
}
