/* Social Media */
.socialmedia {
    margin-top: 10rem;
  }
  .containerSocialmedia {
    display: inline-table;
    margin: auto;
    text-align: -webkit-center;
    width: 100%;
    flex-direction: row;
  }
  .socialmedia h1 {
    color: #356daf;
    font-size: 22px;
  }
  .socialmedia img {
    width: 8vh !important;
    margin: 1rem;
  }
  
  /* Vector */
  .containerVector {
    width: 25px;
    border: 1px solid #161616;
    background-color: #161616;
    border-radius: 20px;
    padding: 0.5rem;
    height: 25px;
    position: fixed;
    bottom: 0;
    right: 0;
  }
  .vector {
    width: 10px;
  }
  .imgVector {
    width: 100%;
    height: 100%;
    display: flex;
  }
  .endVector {
    display: flex;
    justify-content: flex-end;
  }