/* Projects */
.projects {
  margin-top: 5rem;
  width: 100%;
  display: flex;
  justify-content: center;
}
.projects h1 {
  color: #356daf;
  font-size: 22px;
}
.rowProjects {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.buttom-a{
  margin-right: 1rem;
  border: 1px solid #356daf;
  border-radius: 5px;
  padding: 1rem;
}

/*pc*/
@media (min-width: 1024px) {
  .projects img {
    max-height: 250px;
    margin: 1rem;
    display: flex;
    align-self: center;
  }
  .buttom-a{
    font-size: 1em;
  }
}
/*Movil*/
@media (max-width: 768px) {
  .projects img {
    max-height: 200px;
    display: flex;
    align-self: center;
  }
  .buttom-a{
    font-size: 0.8em;
  }
 
}
.project{
  margin-top: 1rem;
}